const Header = ({ restartInterval, setCurIndex, type, setType, coord, setCoord }) => {
  // ,  
  const handleToggle = () => {
    type === "shortly" ? setType("weekly") : setType("shortly");
    setCurIndex(0);
    restartInterval();
  };

  const cities = [
    { name: "", lat: 37, lon: 126 },
    { name: "", lat: 35, lon: 129 },
    { name: "", lat: 37, lon: 126 },
    { name: "", lat: 35, lon: 128 },
    { name: "", lat: 36, lon: 127 },
    { name: "", lat: 35, lon: 126 },
    { name: "", lat: 33, lon: 126 },
  ];

  return (
    <header>
      <div className="typeToggle">
        <button className={type === "shortly" ? "active" : ""} onClick={handleToggle}>
          <span>  </span>
          <span>  </span>
        </button>
      </div>
      <div className="cities">
        {cities.map((city) => (
          <button
            key={city.name}
            className={coord.name === city.name ? "active" : ""}
            onClick={() => {
              setCoord({ lat: city.lat, lon: city.lon, name: city.name });
              setCurIndex(0);
              restartInterval();
            }}
          >
            {city.name.slice(0, 2)}
          </button>
        ))}
      </div>
      <p className="city">{coord.name}</p>
    </header>
  );
};

export default Header;
